.index-form {
  color: black;
  padding-top: 64px;
  padding-bottom: 50px;
  @include pad {
    padding-top: 40px;
  }
  @include mobile {
    padding-top: 24px;
  }
  form {
    width: 66%;
    margin: 0 auto;
    @include pad {
      width: 64.84375%;
    }
    @include mobile {
      width: 87.2%;
    }
    h2 {
      font-size: 48px;
      line-height: 72px;
      text-align: center;
      padding-bottom: 24px;
      @include pad {
        font-size: 32px;
        line-height: 48px;
        padding-bottom: 16px;
      }
      @include mobile {
        font-size: 20px;
        padding-bottom: 8px;
      }
    }
    p {
      font-size: 20px;
      line-height: 30px;
      padding-bottom: 20px;
      @include pad {
        font-size: 16px;
        line-height: 24px;
        padding-bottom: 16px;
      }
      @include mobile {
        padding-bottom: 8px;
      }
    }
    label,
    input:not([type="checkbox"]) {
      display: block;
    }
    label {
      font-size: 16px;
      line-height: 24px;
      padding-bottom: 4px;
    }
    input:not([type="checkbox"]),
    textarea {
      width: 100%;
      padding: 14px 16px;
      border: 1px solid #000000;
      // margin-bottom: 16px;
    }
    textarea {
      height: 136px;
      @include mobile {
        height: 100px;
      }
    }
    textarea,
    input {
      margin-bottom: 16px;
      @include pad {
        margin-bottom: 12px;
      }
    }
    input[type="checkbox"] {
      width: 24px;
      height: 24px;
    }
    span {
      vertical-align: top;
      font-size: 16px;
      line-height: 24px;
    }
    input[type="submit"] {
      width: 22.72727%;
      background-color: #aa0601;
      color: #fff;
      margin: 0 auto;
      font-size: 16px;
      line-height: 24px;
      @include pad {
        width: 27.30923%;
        line-height: 10px;
      }
      @include mobile {
        width: 42.59021%;
      }
    }
  }
}
